import React from 'react'

import { getNumeroMese } from '../functions/functions'

import xGialla from '../img/xGialla.png'
import xBlu from '../img/xBlu.png'

function PromDivComp(props){

  const [hovered, setHovered] = React.useState(false)

  let giorno = ''+ props.promemoria.giorno
  if (giorno.length === 1){ giorno = 0 +''+ giorno }

  let nomeMese = props.promemoria.mese

  let mese = getNumeroMese(nomeMese) //1 è Gennaio.. 12 è Dicembre

  mese = mese +''
  if (mese.length === 1){ mese = 0 +''+ mese }

  function hover(){
    setHovered(prevHovered => !prevHovered)
  }

  //oggetti per lo style
  const stileHovered = { background: 'rgb(252, 226, 78)',
                         borderColor: 'rgb(1, 20, 35)',
                        transform: 'scale(0.9)'
                     }

  return(
    <div className='prom-div-comp'>
      
      <div className='small-closing-div'
          onClick={() => props.removePromemoria(props.promemoria.id)}
          onMouseEnter={hover}
          onMouseLeave={hover}
          style={hovered? stileHovered:{} }
      >
        <img src={hovered? xBlu : xGialla}
              alt=''
              title='cancella promemoria'/>
      </div>
      <p className="testo-p">{props.promemoria.testo}</p>
      <p className="data-p">
         {giorno}/{mese}/{props.promemoria.anno}&nbsp; {props.promemoria.orario}
      </p>

    </div>
  )
}


export default PromDivComp