function Promemoria (id, testo, anno, mese, giorno, orario){
  this.id = id
  this.testo = testo
  this.anno = anno
  this.mese = mese
  this.giorno = giorno
  this.orario = orario
}

function getNumeroGiorni(mese, anno){
  if(mese === 'Febbraio') { return anno%4 === 0 ? 29 : 28 }
  else if(mese === 'Aprile' || mese === 'Giugno' || mese === 'Settembre' || mese === 'Novembre'){ return 30 }
  else { return 31 }
}

function getNumeroMese(nomeMese){
  if(nomeMese === 'Gennaio') {return 1}
  else if(nomeMese === 'Febbraio') {return 2}
  else if(nomeMese === 'Marzo') {return 3}
  else if(nomeMese === 'Aprile') {return 4}
  else if(nomeMese === 'Maggio') {return 5}
  else if(nomeMese === 'Giugno') {return 6}
  else if(nomeMese === 'Luglio') {return 7}
  else if(nomeMese === 'Agosto') {return 8}
  else if(nomeMese === 'Settembre') {return 9}
  else if(nomeMese === 'Ottobre') {return 10}
  else if(nomeMese === 'Novembre') {return 11}
  else {return 12}
}

// restituisce la lista dei promemoria del giorno odierno
function getPromsOdierni(listaPromemoria){

  let oggi = new Date()

  let anno = oggi.getFullYear()
  let mese = oggi.getMonth() // 0 = gennaio.. 11 = dicembre
  mese++
  let giorno = oggi.getDate()

  let listaPromsOdierni = listaPromemoria.filter(item =>
    item.anno === anno &&
    getNumeroMese(item.mese) === mese &&
    parseInt(item.giorno) === giorno
  )

  return listaPromsOdierni
}

//restituisce true se la lista di promemoria 1 è uguale alla lista di prom 2
//confronta i singoli promemoria per id
function listaPromsUguali(lista1, lista2){
  if(lista1.length !== lista2.length) return false
  if(lista1.length === 0 && lista2.length === 0) return true

  for(let i = 0; i< lista1.length; i++){
    if( lista1[i].id !== lista2[i].id ) return false
  }
  return true
}

export{ Promemoria, getNumeroGiorni, getNumeroMese, getPromsOdierni, listaPromsUguali }