import React from 'react'

import PromDivComp from './PromDivComp'
import xGialla from '../img/xGialla.png'
import xBlu from '../img/xBlu.png'

function PromsComp(props){

  const [hovered1, setHovered1] = React.useState(false)
  const [hovered2, setHovered2] = React.useState(false)


  let divsPromemoriaTotali = props.listaPromemoria.map(
    item => <PromDivComp  key={item.id}
                          promemoria={item}
                          removePromemoria={props.removePromemoria}
                          id={item.id}
            />
  )

  let divsPromemoriaOdierni = props.listaPromemoriaOdierni.map(
    item => <PromDivComp  key={item.id}
                          promemoria={item}
                          removePromemoria={props.removePromemoria}
                          id={item.id}
            />
  )

  function hover1(){
    setHovered1(prevHovered1 => !prevHovered1)
  }

  function hover2(){
    setHovered2(prevHovered2 => !prevHovered2)
  }

  //oggetti per lo style
  const nascosto = {display: 'none'}
  const stileHovered = { background: 'rgb(252, 226, 78)',
                    borderColor: 'rgb(1, 20, 35)',
                    transform: 'scale(1)'
                  }

  return(
    <div className='proms-comp' style={props.visibile? {} : nascosto}>
      
      <div className="prom-totali">
        <div className='top-div'>
          <h1>Lista promemoria</h1>
          <div className='close-div'
            onClick={props.cambiaVisibilita1}
            onMouseEnter={hover1}
            onMouseLeave={hover1}
            style={hovered1? stileHovered:{} }
          >
            <img src={hovered1? xBlu : xGialla}
                  alt=''
                  title='torna al form'
            />
            
          </div>
        </div>

        <p className='vuoto'
            style={props.listaPromemoria.length === 0? {} : nascosto}
        >
          Non ci sono promemoria salvati..
        </p>

        {divsPromemoriaTotali}

      </div>

      <div className="separatore"></div>
      
      <div className="proms-odierni">

        <div className='top-div top-div-odierni'>
          <h1>Promemoria odierni</h1>
          <div className='close-div close-div-odierni'
            onClick={props.cambiaVisibilita1}
            onMouseEnter={hover2}
            onMouseLeave={hover2}
            style={hovered2? stileHovered:{} }
          >
            <img src={hovered2? xBlu : xGialla}
                  alt=''
                  title='torna al form'
            />
            
          </div>
        </div>

        <p className='vuoto'
            style={props.listaPromemoriaOdierni.length === 0? {} : nascosto}
        >
          Non ci sono promemoria odierni..
        </p>

        {divsPromemoriaOdierni}

      </div>

      
    </div>
  )

}


export default PromsComp