import React from 'react'

import CalendarDayComp from './CalendarDayComp'
import { getNumeroGiorni, getNumeroMese } from '../functions/functions'


class CalendarComp extends React.Component{

  constructor(){
    super()
    this.state={
      annoSelezionato : 2020,
      meseSelezionato : 'Gennaio'
    }

    this.handleChange = this.handleChange.bind(this)

  }

  handleChange(event){
    this.setState(
      {[event.target.name] : event.target.value}
    )
  }

  
  render(){

    //lista contenente i promeria di anno = annoSelezionato e mese = meseSelezionato
    let listaPromMeseOdierno = []

    let listaPromemoria = this.props.listaPromemoria
    for(let i = 0; i < listaPromemoria.length; i++){
      let promCorrente = listaPromemoria[i]
      if( promCorrente.anno === this.state.annoSelezionato &&
          promCorrente.mese === this.state.meseSelezionato ){
            listaPromMeseOdierno.push(promCorrente)
          }
    }

    let numGiorni = getNumeroGiorni(this.state.meseSelezionato, this.state.annoSelezionato)
    let divsGiorni = []
    for(let i = 1; i <= numGiorni; i++){
      let key = this.state.annoSelezionato + this.state.meseSelezionato + i
      
      let promPresente = false
      for(let j = 0; j < listaPromMeseOdierno.length; j++){
        
        if(parseInt(listaPromMeseOdierno[j].giorno) === i){
          promPresente = true
        }
      }

      let data = i+ '/'+ this.state.meseSelezionato +'/'+ this.state.annoSelezionato
      divsGiorni.push(<CalendarDayComp key={key} 
                                      giorno={i}
                                      promPresente={promPresente}
                                      data = {data}
                                      cambiaCalendarDataClicked = {this.props.cambiaCalendarDataClicked}
                                      cambiaVisibilita2 = {this.props.cambiaVisibilita2}
                      />)
    }

    let numeroMese = getNumeroMese(this.state.meseSelezionato)

    let date = this.state.annoSelezionato +'-'+ numeroMese +'-01'
    let numDivVuoti = new Date(date).getDay() -1

    //se il primo giorno del mese selezionato è domenica, devono esserci 6 spazi vuoti
    if(numDivVuoti === -1){ numDivVuoti = 6}
    for(let i = 0; i < numDivVuoti; i++){
      let key = this.state.annoSelezionato + this.state.meseSelezionato + (i+32)
      divsGiorni.unshift(<div key={key}/>)
    }

    const nascosto = {display: 'none'}
    return(
      <div className='calendar-comp' style={this.props.visibile? {} : nascosto}>

        <h1>Calendario</h1>

        <span>
        <label> Anno:
          <input type="number" name="annoSelezionato" min='1950' max="2090" value={this.state.annoSelezionato} onChange={this.handleChange}/>
        </label>
        </span>

        <span>
        <label> Mese:
            <select value={this.meseSelezionato} name="meseSelezionato"
              onChange={this.handleChange}>
              <option value="Gennaio">Gennaio</option>
              <option value="Febbraio">Febbraio</option>
              <option value="Marzo">Marzo</option>
              <option value="Aprile">Aprile</option>
              <option value="Maggio">Maggio</option>
              <option value="Giugno">Giugno</option>
              <option value="Luglio">Luglio</option>
              <option value="Agosto">Agosto</option>
              <option value="Settembre">Settembre</option>
              <option value="Ottobre">Ottobre</option>
              <option value="Novembre">Novembre</option>
              <option value="Dicembre">Dicembre</option>
            </select>
        </label>
        </span>
        
        <div className="div-calendario">
        <div><p>LUN</p></div>
        <div><p>MAR</p></div>
        <div><p>MER</p></div>
        <div><p>GIO</p></div>
        <div><p>VEN</p></div>
        <div><p>SAB</p></div>
        <div><p>DOM</p></div>
          {divsGiorni}
        </div>
          
      </div>
    )
  }


}

export default CalendarComp