import React from 'react'

import DayDivComp from './DayDivComp'
import xGialla from '../img/xGialla.png'
import xBlu from '../img/xBlu.png'

function DaysComp(props){

  const [hovered, setHovered] = React.useState(false)

  let divsPromemoria = props.listaPromsClickedDay.map(
    item => <DayDivComp  key={item.id}
                          promemoria={item}
                          removePromemoria={props.removePromemoria}
                          id={item.id}
            />
  )

  function hover(){
    setHovered(prevHovered => !prevHovered)
  }

  const stileHovered = { background: 'rgb(252, 226, 78)',
                    borderColor: 'rgb(1, 20, 35)',
                    transform: 'scale(1)'
                  }

  //oggetti per lo style
  const nascosto = {display: 'none'}

  return(
    <div className='proms-comp days-comp-width' style={props.visibile? {} : nascosto}>
      
      <div className='top-div'>
        <h1>{props.data}</h1>
        <div className='close-div'
          onClick={props.cambiaVisibilita2}
          onMouseEnter={hover}
          onMouseLeave={hover}
          style={hovered? stileHovered:{} }
        >
          <img src={hovered? xBlu : xGialla}
                alt=''
                title='torna al calendario'
          />
          
        </div>
      </div>

      <p className='vuoto'
            style={props.listaPromsClickedDay.length === 0? {} : nascosto}
      >
          Non ci sono promemoria per questo giorno
      </p>

      {divsPromemoria}

    </div>
  )
}

export default DaysComp