import React from 'react'

import FormComp from './FormComp'
import PromsComp from './PromsComp'
import CalendarComp from './CalendarComp'
import DaysComp from './DaysComp'

import {getPromsOdierni, listaPromsUguali} from '../functions/functions'

class ContainerComp extends React.Component{
  
  constructor(){
    super()
    this.state={
      listaPromemoria : [],
      listaPromsOdierni: [], //lista dei promemoria del giorno odierno
      idPromemoriaChecking: 0, //id del setInterval che viene impostato in componentDidMount
      formVisibile : true,
      calendarVisibile: true,
      calendarDataClicked: ''
    }
    this.addPromemoria = this.addPromemoria.bind(this)
    this.removePromemoria = this.removePromemoria.bind(this)
    this.cambiaVisibilita1 = this.cambiaVisibilita1.bind(this)
    this.cambiaVisibilita2 = this.cambiaVisibilita2.bind(this)
    this.cambiaCalendarDataClicked = this.cambiaCalendarDataClicked.bind(this)
  }

  addPromemoria(promemoria){

    this.setState( prevState =>{
      let listaPromemoriaCopy = [...prevState.listaPromemoria]
      listaPromemoriaCopy.push(promemoria)
      return{ listaPromemoria : listaPromemoriaCopy}
    })
  }

  //rimuove un promemoria per id
  removePromemoria(id){
    for(let i = 0; i < this.state.listaPromemoria.length; i++){
      
      if( id === this.state.listaPromemoria[i].id ){

        this.setState(prevState =>{
          let listaPromemoriaCopy = [...prevState.listaPromemoria]
          listaPromemoriaCopy.splice(i,1);
          return{ listaPromemoria: listaPromemoriaCopy }
        })

        break;
      }

    }
  }

  cambiaVisibilita1(){

    this.setState(
      prevState => {
        return{ formVisibile : !prevState.formVisibile }
      })

  }

  cambiaVisibilita2(){

    this.setState(
      prevState => {
        return{ calendarVisibile : !prevState.calendarVisibile }
      })

  }

  cambiaCalendarDataClicked(data){
    this.setState( {calendarDataClicked : data} )
  }


  componentDidMount(){
    const idChecking = setInterval(()=>{
        let listaPromsOdierniUpdated = getPromsOdierni(this.state.listaPromemoria)
        if( !listaPromsUguali(this.state.listaPromsOdierni, listaPromsOdierniUpdated) ){
          this.setState( {listaPromsOdierni: listaPromsOdierniUpdated} )
        }
    }, 5000)

    this.setState({idPromemoriaChecking : idChecking})
  }

  componentDidUpdate(prevProps, prevState){
    if( !listaPromsUguali(prevState.listaPromemoria, this.state.listaPromemoria) ){
      let listaPromsOdierniUpdated = getPromsOdierni(this.state.listaPromemoria)
      this.setState({listaPromsOdierni: listaPromsOdierniUpdated})
    }

  }

  componentWillUnmount(){
    clearInterval(this.state.idPromemoriaChecking)
  }

  render(){
    
    let firstSlashIndex = this.state.calendarDataClicked.indexOf('/')
    let secondSlashIndex = this.state.calendarDataClicked.lastIndexOf('/')

    let giorno = parseInt( this.state.calendarDataClicked.substring(0, firstSlashIndex) )
    let mese = this.state.calendarDataClicked.substring(firstSlashIndex+1, secondSlashIndex)
    let anno = parseInt( this.state.calendarDataClicked.substring(secondSlashIndex+1) )

    let listaPromsClickedDay = []

    for(let i = 0; i<this.state.listaPromemoria.length; i++){
      let promCorrente = this.state.listaPromemoria[i]
      if( parseInt(promCorrente.anno) === anno &&
          promCorrente.mese === mese &&
          parseInt(promCorrente.giorno) === giorno ){
           listaPromsClickedDay.push(promCorrente) 
          }
    }
    
    return(
    
      <div className='container-comp'>
        <FormComp addPromemoria={this.addPromemoria}

                cambiaVisibilita1={this.cambiaVisibilita1}
                visibile={this.state.formVisibile}
        />
        
        <PromsComp listaPromemoria={this.state.listaPromemoria}
                  listaPromemoriaOdierni={this.state.listaPromsOdierni}
                  removePromemoria={this.removePromemoria}

                cambiaVisibilita1={this.cambiaVisibilita1}
                visibile={!this.state.formVisibile}
        />

        <CalendarComp listaPromemoria={this.state.listaPromemoria}
                      removePromemoria={this.removePromemoria}
                      cambiaCalendarDataClicked={this.cambiaCalendarDataClicked}

                      cambiaVisibilita2={this.cambiaVisibilita2}
                      visibile={this.state.calendarVisibile}
        />

        <DaysComp
                      listaPromsClickedDay={listaPromsClickedDay}
                      data={this.state.calendarDataClicked}
                      removePromemoria={this.removePromemoria}

                      cambiaVisibilita2={this.cambiaVisibilita2}
                      visibile={!this.state.calendarVisibile}/>

      </div>
    )
  }

}

export default ContainerComp