import React from 'react'

function CalendarDayComp(props){

  return(
    <div className='calendar-day-comp'
          onClick={() =>{
                      props.cambiaCalendarDataClicked(props.data)
                      props.cambiaVisibilita2()
                  }
          }
    >

        <h2 className={props.promPresente? 'evidenziato' : {}}>
          {props.giorno}
        </h2>

    </div>
  )
}

export default CalendarDayComp