import React from 'react'

import {Promemoria, getNumeroGiorni} from '../functions/functions'

import frecciaSu from '../img/frecciaSu.png'
import frecciaSinistra from '../img/frecciaSinistra.png'

class FormComp extends React.Component{
  
  constructor(){
    super()
    this.state ={
      textAreaValue : 'Descrivi il promemoria..',
      textAreaNeedVisibile: false,
      giorno : '1',
      mese : 'Gennaio',
      anno : 2020,
      orario : '00:00',
      orarioNeedVisibile: false,
      idPromemoria: 0,
      notificaAnimation: 'none'
    }

    this.handleChange = this.handleChange.bind(this)
    this.handlePromemoria = this.handlePromemoria.bind(this)
  }

  handleChange(event){
    this.setState(
      { [event.target.name] : event.target.value }
    )

    if((event.target.name) === 'textAreaValue'){
      this.setState({textAreaNeedVisibile : false})
    }else if((event.target.name) === 'orario'){
      this.setState({orarioNeedVisibile : false})
    }

  }

  handlePromemoria(){
    if(this.state.textAreaValue === ''){
      this.setState( {textAreaNeedVisibile : true} )
    }else if(this.state.orario === ''){
      this.setState( {orarioNeedVisibile : true} )
    }else if(this.state.anno < 1950 || this.state.anno > 2090){
      return
    }else if(this.state.giorno < 1 ||
              this.state.giorno > getNumeroGiorni(this.state.mese, this.state.anno)){
      return
    }else{
      let p = new Promemoria(this.state.idPromemoria, this.state.textAreaValue, this.state.anno,
        this.state.mese, this.state.giorno, this.state.orario)
        
      this.props.addPromemoria(p)

      this.setState( prevState => {
        return{ idPromemoria : prevState.idPromemoria+1}
      })

      this.setState( {notificaAnimation: 'visible-to-not-visible'} )

      setTimeout( ()=>{
        this.setState( {notificaAnimation: 'none'} ) }
        ,1000)

    }
  }

  render(){
    const nascosto = {display: 'none'}

    let notificaStyle = {animation: this.state.notificaAnimation +' 1s linear 1'}
    return(
      <div className='form-comp' style={this.props.visibile? {} : nascosto}>

        <h1>Crea Premomoria</h1>

        <textarea name="textAreaValue"
                  value = {this.state.textAreaValue}
                  onChange={this.handleChange}/>

        <div className='text-area-need'
          style={this.state.textAreaNeedVisibile? {} : nascosto}
        >
          <img src={frecciaSu} alt=""/>
          <p>inserisci descrizione promemoria</p>
        </div>
        <label> Anno:
          <input type="number" name="anno" min='1950' max="2090" value={this.state.anno} onChange={this.handleChange}/>
        </label>

        <label> Mese:
            <select value={this.state.mese} name="mese"
              onChange={this.handleChange}>
              <option value="Gennaio">Gennaio</option>
              <option value="Febbraio">Febbraio</option>
              <option value="Marzo">Marzo</option>
              <option value="Aprile">Aprile</option>
              <option value="Maggio">Maggio</option>
              <option value="Giugno">Giugno</option>
              <option value="Luglio">Luglio</option>
              <option value="Agosto">Agosto</option>
              <option value="Settembre">Settembre</option>
              <option value="Ottobre">Ottobre</option>
              <option value="Novembre">Novembre</option>
              <option value="Dicembre">Dicembre</option>
            </select>
        </label>

        <label> Giorno:
          <input type="number" name="giorno" value={this.state.giorno} onChange={this.handleChange}
          min="1" max={getNumeroGiorni(this.state.mese, this.state.anno)}/>
        </label>

        <div className='orario-div'>
          <label> Orario:
            <input type="time" name="orario" value={this.state.orario} onChange={this.handleChange} />
          </label>

          <div className='orario-need'
          style={this.state.orarioNeedVisibile? {} : nascosto}
          >
          <img src={frecciaSinistra} alt=""/>
          <p>inserisci<br/>
              orario<br/>
              valido
          </p>
          </div>
        </div>

        <div>

          <button onClick={this.handlePromemoria}>Aggiungi Promemoria</button>
          <p className='notifica' style={notificaStyle}>PROMEMORIA AGGIUNTO!</p>
          <button onClick={this.props.cambiaVisibilita1}>Promemoria salvati..</button>

        </div>
        
      </div>
    )

  }
}

export default FormComp